<template>
	<view class="infor" :style="{height:containerHeight}" @touchmove.prevent>
		<view class="fields">
			<uni-forms ref="valiForm" :rules="rules" :modelValue="valiFormData">
				<uni-forms-item class="field" :required="required.vin" label="VIN" name="vin">
					<uni-easyinput :disabled="!clusterShow" v-model="valiFormData.vin" placeholder="Please Enter" />
				</uni-forms-item>
				<uni-forms-item class="field" :required="required.tqs" label="TQS" name="TQS">
					<picker @change="selectTQS" :range="tqsList" range-key="tqs" :disabled="!clusterShow">
						<view v-if="!valiFormData.tqs">Please Select</view>
						<view v-else>
							<view>{{valiFormData.tqs}}</view>
						</view>
					</picker>
				</uni-forms-item>
				<uni-forms-item class="field" :required="required.cluster" label="Cluster" name="cluster"
					v-show='clusterShow'>
					<picker @change="selectTestCluster" :range="testClusterList">
						<view v-if="!valiFormData.cluster">Please Select</view>
						<view v-else>
							<view>{{valiFormData.cluster}}</view>
						</view>
					</picker>
				</uni-forms-item>
				<uni-forms-item class="field" label="Phase" name="phase">
					<uni-easyinput :disabled="!clusterShow" v-model="valiFormData.phase" placeholder="Please Enter" />
				</uni-forms-item>
				<uni-forms-item class="field" label="Special Vehicles" :required="required.specialVehicles"
					name="specialVehicles">
					<uni-easyinput :disabled="!clusterShow" v-model="valiFormData.specialVehicles"
						placeholder="Please Enter" />
				</uni-forms-item>
				<uni-forms-item class="field" label="VR" :required="required.vr" name="vr">
					<uni-easyinput :disabled="disabled.vr" v-model="valiFormData.vr" placeholder="Please Enter" />
				</uni-forms-item>
				<uni-forms-item class="field" label="Test Start Time" :required="required.testStartTime"
					name="testStartTime">
					<picker mode="date" ref="datePicker" :value="valiFormData.testStartTime" @change="change"
						:disabled="disabled.testStartTime">
						<view v-if='!valiFormData.testStartTime'>Please Select</view>
						<view v-else class="uni-input">{{valiFormData.testStartTime}}</view>
					</picker>
				</uni-forms-item>
			</uni-forms>
			<view style="display: flex;margin-top: 30px;">
				<button type="default" size="default" style="background-color: #e6eaeb;width: 60%;margin: 5px;"
					@click="back">Close</button>
				<button type="primary" size="default" style="background-color: #193c47;width: 60%;margin: 5px;"
					@click="submit">OK</button>
			</view>
			<view>
			</view>
		</view>
	</view>
</template>

<script>
	import bus from '../task/temporaryBus.js'

	function debounce(func, wait) {
		let timeout;
		return function(...args) {
			const context = this;
			clearTimeout(timeout);
			timeout = setTimeout(() => func.apply(context, args), wait);
		};
	}
	export default {
		components: {},
		props: {},
		data() {
			return {
				show: false,
				containerHeight: '800px',
				tempTestStartTime: "Please Choose",
				// tqs:'',
				valiFormData: {}, //基础数据
				url: {
					// tqsList: '/testService/qaTqsGroupContent/list',
					getTestCluster: '/testService/qaTestResult/getTestCluster',
					resultList: '/testService/qaTestResult/appList',
				},
				required: {
					vin: false,
					tqs: true,
					cluster: false,
					phase: false,
					specialVehicles: false,
					vr: false,
					testStartTime: true
				},
				disabled: {
					vin: false,
					phase: false,
					cluster: false,
					specialVehicles: false,
					vr: false,
					testStartTime: false
				},
				// 校验规则
				requiredArr: [
					'testStartTime',
					'tqs'
				],
				rules: {
					testStartTime: {
						rules: [{
							required: true,
							errorMessage: 'Test Start Time Cannot Be Empty.'
						}]
					},
					tqs: {
						rules: [{
							required: true,
							errorMessage: 'Tqs Cannot Be Empty.'
						}]
					},
				},
				index: 0,
				clusterShow: true,
				tqsList: [],
				testClusterList: [],
				operate: ''

			}
		},

		onLoad(e) {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
				plus.navigator.setStatusBarStyle("dark")
			}
			this.getScreenHeight()
			if (e.data != undefined && e.data != null && e.data != null) {
				this.valiFormData = JSON.parse(e.data)
				if (e.operate == 'scan') {
					this.operate = 'scan'
					// this.tqsList = [...this.valiFormData.tqsList]
					this.tqsList = this.valiFormData.tqsList.filter((currentValue, currentIndex, sourceArr) => {
						return sourceArr.findIndex(x => x.tqs === currentValue.tqs) === currentIndex
					});
				} else {
					this.operate = 'edit'
				}
			}
			this.loadTestStartTime(this.valiFormData.testStartTime)
			this.getTestClusterList()
			uni.setLocale('en');
		},
		watch: {},
		mounted() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
				plus.navigator.setStatusBarStyle("dark")
			}
		},
		onShow() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
				plus.navigator.setStatusBarStyle("dark")
			}
			// this.getTqsList()
		},
		methods: {
			// getTqsList() {
			// 	let parameter = {
			// 		pageSize: 50
			// 	}
			// 	this.$http.get(this.url.tqsList, {
			// 		params: parameter
			// 	}).then(res => {
			// 		if (res.data.success) {
			// 			res.data.result.records.forEach(item => {
			// 				this.tqsList.push(item.tqs)
			// 			})
			// 		}
			// 	})
			// },
			selectTQS(e) {
				let that = this
				this.index = e.detail.value
				this.$set(this.valiFormData, 'tqs', this.tqsList[e.detail.value].tqs)
				let params = {
					id: this.tqsList[e.detail.value].id
				}
				this.$http.get(this.url.resultList, {
					params: params
				}).then(res => {
					if (res.data.success) {
						this.valiFormData = res.data.result[0]
					}
				})
			},
			getTestClusterList() {
				if (this.operate == 'edit') {
					this.clusterShow = false
				} else {
					this.clusterShow = true
				}
				this.$http.get(this.url.getTestCluster).then(res => {
					if (res.data.success) {
						this.testClusterList = res.data.result.map(item => item.item_text)
					}
				})
			},

			selectTestCluster(e) {
				this.$set(this.valiFormData, 'cluster', this.testClusterList[e.detail.value])
			},
			back() {
				uni.navigateBack();
			},
			//debounce防抖 防止连续多次点击
			submit: debounce(function(event) {
				let that = this;
				that.$bus.$emit('tqsIdEvent', that.valiFormData);
				//提交时间完成后 进行页面跳转
				that.$refs.valiForm.validateField(that.requiredArr).then((res) => {
					if (that.valiFormData != null) {
						for (let key in that.valiFormData) {
							let value = that.valiFormData[key];
							if (value != undefined && value != null && value != '') {
								if (value.length == 10 && value.indexOf('/') == 4 && value.lastIndexOf(
										'/') == 7) {
									that.valiFormData[key] = value.replaceAll("/", "-");
								}
							}
						}
						//从测试日历进入，一定有临时测试结果，从扫码进入，可能没有临时测试结果数据，需要生成  is_temp = 0,calendar_id == '扫码'
						// //有临时测试结果的时候  id有值，没有测试结果id没有值
						that.$http.post(`/testService/qaTestResult/insertOrUpdate`, that.valiFormData)
							.then(res => {
								if (res.data.success) {
									uni.showToast({
										title: 'Save Successfully',
									});
									let newData = res.data.result
									setTimeout(function() {
										uni.navigateTo({
											url: '../task/checkItems?data=' + JSON
												.stringify(newData)
										})
									}, 500)
								}
							})
					}
				}).catch((err) => {
					console.log('err', err);
				})
			}, 1000),
			loadTestStartTime(value) {
				let that = this
				if (value != undefined && value != null) {
					that.tempTestStartTime = that.getDate('', value, 'date');
				} else {
					that.tempTestStartTime = "Please Choose";
				}
			},
			close() {
				let that = this;
				that.show = false;
			},
			confirm(e) {
				let that = this;
				console.log(e.value)
				// that.valiFormData.testStartTime = that.getDate('', e.value, e.mode)
				this.$set(that.valiFormData, 'testStartTime', that.getDate('', e.value, e.mode))
				console.log(that.valiFormData.testStartTime)
				that.loadTestStartTime(that.valiFormData.testStartTime)
				// this.$set(that.valiFormData,'testStartTime',e.value)
				that.show = false
			},
			change(e) {
				this.valiFormData.testStartTime = e.detail.value
				console.log(e)
			},
			getDate(type, value, format) {
				const date = new Date(value);
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();
				let hours = date.getHours();
				let minutes = date.getMinutes();
				let seconds = date.getSeconds();
				if (type === 'start') {
					year = year;
				} else if (type === 'end') {
					year = year + 10;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
				hours = hours > 9 ? hours : '0' + hours;
				minutes = minutes > 9 ? minutes : '0' + minutes;
				seconds = seconds > 9 ? seconds : '0' + seconds;
				switch (format) {
					case 'date':
						return `${year}-${month}-${day}`;
						break;
					case 'datetime':
						return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
						break;
					default:
						return `${year}-${month}-${day}`;
						break;
				}
			},
			showPicker() {
				let that = this
				if (that.show == false) {
					that.$refs.datePicker.innerValue = new Date().getTime()
					that.show = true
				} else {
					that.show = false
				}
			},
			getScreenHeight() {
				uni.getSystemInfo({
					success: (res) => {
						this.containerHeight = res.windowHeight + 'px';
						console.log(this.containerHeight)
					}
				});
			},
		}

	}
</script>

<style lang="scss" scoped scoped>
	.infor {
		background-color: #f0f3f4;
		padding-top: env(safe-area-inset-top);
		/* 兼容 iOS 11.0-11.4 */
		padding-top: constant(safe-area-inset-top);
		/* 兼容 iOS 12.0-12.1 */
	}

	.fields {
		background-color: #fff;
		border-radius: 10px;
		margin: 20px;
		padding: 20px;
	}

    .field {
        padding: 0px 0px 20px 0px;
        border-bottom: 2px solid #f1f1f1;
        text-align: right;

        ::v-deep .uni-easyinput {
            text-align: right !important;
        }

        ::v-deep .uni-forms-item__label {
            width: 40% !important;
        }
    }

    ::v-deep .u-transition .u-slide-up-enter-to .u-slide-up-enter-active {
        height: 30% !important;
    }

    ::v-deep .is-disabled {
        background: #fff !important;
    }
</style>